import styled from 'styled-components'

import {
    theme
} from '../constants'

import {
    above,
    under
} from '../helpers/breakpoints'

export const StyledNavbarWrapper = styled.header`
    width: 100%;
    height: auto;

    z-index: 900;

    ${theme.transitions.quick}

    background-color: ${theme.colorBeige};

    ${props => props.windowScrolled ? `box-shadow: ${theme.boxShadow.small};` : ''}

    @media ${above.tablet} {
        position: fixed;
        top: 0;
        left: 0;
    }
`

export const StyledNavbar = styled.nav`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    width: 100%;

    margin: 0;

    @media ${above.tablet} {
        flex-direction: row;
    }
`

export const StyledLogoWrapper = styled.a`
    margin: ${theme.s()} 0;

    font-family: ${theme.monoFont};
    color: ${theme.colorPrimary};

    display: flex;
    flex-direction: row;
    align-items: center;

    ${theme.transitions.ease}

    &:hover {
        transform: translateY(-3px);
    }
`

export const StyledLogo = styled.img`
    max-width: ${props => props.windowScrolled ? 48 : 64}px;
    height: auto;

    ${theme.transitions.ease}

    margin: 0;
    margin-right: ${theme.s(2)};

    @media ${under.tablet} {
        max-width: ${props => props.windowScrolled ? 48 : 80}px;
    }
`

export const StyledLogoDesc = styled.p`
    margin: 0;

    font-size: ${props => props.windowScrolled ? 1 : 1.35}rem;

    ${theme.transitions.ease}

    display: flex;
    flex-direction: column;

    span:nth-child(2) {
        position: relative;

        &:after {
            content: '';

            width: 100%;
            height: 30%;

            background-color: ${theme.colorOrange};

            position: absolute;
            bottom: 5%;
            left: 0;

            z-index: -1;
        }
    }

    @media ${under.tablet} {
        font-size: ${props => props.windowScrolled ? 1 : 1.8}rem;
    }
`

export const StyledNav = styled.ul`
    list-style: none;
    padding: 0;

    margin: ${theme.s()} 0;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    font-family: ${theme.monoFont};

    li {
        display: block;
        margin: 0 ${theme.s(2)};
        padding: ${theme.s()} 0;

        &:last-child {
            margin-right: 0;
        }
    }
`
